import React, {useState, useEffect} from 'react'
import db from '../Firebase/config';
import {Col, Container, Row} from 'reactstrap'
import ArrowForwardIcon from '@material-ui/icons/ArrowForward';

export default function CertificationMain() {
    const [certificates, setCertificates] = useState([]);

    useEffect(() => {
        db.collection("certificate").onSnapshot((snapshot) =>
            setCertificates(snapshot.docs.map((doc) => doc.data()))
        );
    }, [])
    const [certifications, setCertifications] = useState([]);

    useEffect(() => {
        db.collection("certificates").onSnapshot((snapshot) =>
            setCertifications(snapshot.docs.map((doc) => doc.data()))
        );
    }, [])
    return (
        <div className='certificationMain'>
            {certificates.map((certificate) => (
                <div className="certImg">
                    <img src={certificate.img} alt="Certificate Image" />
                </div>
            ))}
            <Container>
                <Row>
                    {certifications.map((certification, index) =>(
                        <Col md='6' key={index}>
                            <div className="myCertificate">
                                <a href={certification.certificateLink} target='_blank'>
                                  <h3>{certification.certificateName}</h3>  <ArrowForwardIcon />
                                </a>
                                <p>{certification.certificateDesc}</p>
                            </div>
                        </Col>
                    ))}
                </Row>
            </Container>
        </div>
    )
}
