import React, {useState, useEffect} from 'react'
import {Col, Container, Row} from 'reactstrap'
import GitHubIcon from '@material-ui/icons/GitHub';
import LinkIcon from '@material-ui/icons/Link';
import db from '../Firebase/config';


export default function Project() {
    const [projects, setProjects] = useState([]);

    useEffect(() => {
        db.collection("project").onSnapshot((snapshot) =>
            setProjects(snapshot.docs.map((doc) => doc.data()))
        );
    }, [])

    return (
        <>
            <Container>
                <Row>
                    {projects.map((project, index) => (
                        <Col md='12' key={index}>
                            <div className="projectCard">
                                <img className='projectImage' src={project.img} alt="Portfolio Image" />
                                <div className="definition">
                                    <div className="projectInfo">
                                        <h3>{project.projectName}</h3>
                                        <p>{project.projectDesc}</p>
                                    </div>
                                    <div className="projectButtons">
                                        <a href={project.code} className="buttons" target='_blank'>
                                            Source <GitHubIcon />
                                        </a>
                                        <a href={project.demo} className="buttons" target='_blank'>
                                            Demo <LinkIcon />
                                        </a>
                                    </div>
                                </div>
                            </div>
                        </Col>
                    ))}
                </Row>
            </Container>
        </>
    )
}
