import React, {useState, useEffect} from 'react'
import db from '../Firebase/config';
import {Col, Container, Row} from 'reactstrap'


export default function AboutMain() {
    const [informations, setInformations] = useState([]);

    useEffect(() => {
        db.collection("about").onSnapshot((snapshot) =>
            setInformations(snapshot.docs.map((doc) => doc.data()))
        );
    }, [])
    return (
        <div className='aboutmain'>
            <Container>
                {informations.map(information => (
                <Row>
                    <Col md='6'>
                        <div className="img">
                            <img src={information.img} alt="Mazhardinov Samandar" />
                        </div>
                    </Col>
                    <Col md='6'>
                        <div className="content">
                            <h2>Mazhardinov Samandar</h2>
                            <p>{information.text1}</p>
                            <h2 className='question'>{information.question}</h2>
                            <p>{information.answer}</p>
                        </div>
                    </Col>
                </Row>
                ))}
            </Container>
        </div>
    )
}
