import React, {useState, useEffect} from 'react';
import db from '../Firebase/config';
import { Link } from 'react-router-dom';
import FacebookIcon from '@material-ui/icons/Facebook';
import GitHubIcon from '@material-ui/icons/GitHub';
import LinkedInIcon from '@material-ui/icons/LinkedIn';
import TelegramIcon from '@material-ui/icons/Telegram';
import Reveal from 'react-reveal/Reveal'

const AboutInfo = () => {
    const [portfolios, setPortfolios] = useState([]);

    useEffect(() => {
        db.collection("portfolio").onSnapshot((snapshot) =>
            setPortfolios(snapshot.docs.map((doc) => doc.data()))
        );
    }, [])
    return (
        <div className='about-info'>
            {portfolios.map(portfolio => (
                <>
                    <div className="side2">
                        <img 
                            className="myImage" 
                            src={portfolio.img} 
                            alt="my image" 
                        />
                    </div>
                    <div className="side1">
                        <Reveal effect="fadeInup" duration={1250} className='fadeInup'>
                            <h1>{portfolio.title}</h1>
                            <p>{portfolio.description}</p>
                        </Reveal>
                       
                        <div className="buttons">
                            <Link className="linkBtn" to="/about-me">
                                About me
                            </Link>
                            <Link className="linkBtn" to="/portfolio">
                                Portfolio
                            </Link>
                        </div>
                        <div className="socials">
                            <a href="https://www.linkedin.com/in/samandar-mazxardinov/" target='_blank'>
                                <LinkedInIcon className="iconSvg" />
                            </a>
                            <a href="https://www.facebook.com/samandar.mazhardinov.9/" target='_blank'>
                                <FacebookIcon className="iconSvg" />
                            </a>
                            <a href="https://github.com/Mazhardinov" target='_blank'>
                                <GitHubIcon className="iconSvg" />
                            </a>
                            <a href="https://t.me/MazhardinovSamandar" target='_blank'>
                                <TelegramIcon className="iconSvg" />
                            </a>
                        </div>
                    </div>
                </>
            ))}
        </div>
    )
}

export default AboutInfo;